import React, { Component } from 'react';

class AsideFooter extends Component {
  clickMenu(event){
    event.preventDefault();
  }
    render() {
        return (
         
        <div className="nav-footer shadow-top">
          <a href="/" onClick={(event)=>this.clickMenu(event)} data-action="toggle" data-class="nav-function-minify" className="hidden-md-down">
            <i className="ni ni-chevron-right" />
            <i className="ni ni-chevron-right" />
          </a>
          <ul className="list-table m-auto nav-footer-buttons">
            <li>
              <a href="/" onClick={(event)=>this.clickMenu(event)} data-toggle="tooltip" data-placement="top" data-original-title="Chat với tôi">
                <i className="fal fa-comments"></i>
              </a>
            </li>
            <li>
              <a href="/" onClick={(event)=>this.clickMenu(event)} data-toggle="tooltip" data-placement="top" data-original-title="Hỗ trợ Chat">
                <i className="fal fa-life-ring" />
              </a>
            </li>
            <li>
              <a href="/" onClick={(event)=>this.clickMenu(event)} data-toggle="tooltip" data-placement="top" data-original-title="Gọi cho tôi">
                <i className="fal fa-phone" />
              </a>
            </li>
          </ul>
        </div>
        );
    }
}

export default AsideFooter;