import React, { Component } from 'react';
import AsideMenu from './AsideMenu';
import AsideLogo from './AsideLogo';
import AsideNaviSearch from './AsideNaviSearch';
import AsideFooter from './AsideFooter';
import AsideInfo from './AsideInfo';

export class Aside extends Component {
  clickMenu(event){
    event.preventDefault();
  }
  render() {
    return (
      <aside className="page-sidebar">

      <AsideLogo/>
        {/* BEGIN PRIMARY NAVIGATION */}

        <nav id="js-primary-nav" className="primary-nav js-list-filter" role="navigation">
          <AsideNaviSearch/>
          <AsideInfo/>
          <AsideMenu/> 
          {/* <div className="filter-message js-filter-message bg-success-600" /> */}
        </nav>
        
        {/* END PRIMARY NAVIGATION */}
        <AsideFooter/>

      </aside>

    );

  }
}

export default Aside;
