import React, { Component } from 'react';
import HeaderMyApps from './HeaderMyApps';
import HeaderNotifi from './HeaderNotifi';
import HeaderAccount from './HeaderAccount';
import HeaderSearch from './HeaderSearch';

export class Header extends Component {
    clickMenu(event){
      event.preventDefault();
    }
    render() {
        return (
         <header className="page-header" role="banner">
      {/* we need this logo when user switches to nav-function-top */}
      <div className="page-logo">
        <a href="/" className="page-logo-link press-scale-down d-flex align-items-center position-relative" data-toggle="modal" data-target="#modal-shortcut">
          <img src="img/logo.png" alt="SmartAdmin WebApp" aria-roledescription="logo" />
          <span className="page-logo-text mr-1">SmartAdmin WebApp</span>
          <span className="position-absolute text-white opacity-50 small pos-top pos-right mr-2 mt-n2" />
          <i className="fal fa-angle-down d-inline-block ml-1 fs-lg color-primary-300" />
        </a>
      </div>
      {/* DOC: nav menu layout change shortcut */}
      <div className="hidden-md-down dropdown-icon-menu position-relative">
        <a href="/" onClick={(event)=>this.clickMenu(event)} className="header-btn btn js-waves-off" data-action="toggle" data-class="nav-function-hidden" title="Ẩn">
          <i className="ni ni-menu" />
        </a>
        <ul>
          <li>
            <a href="/" onClick={(event)=>this.clickMenu(event)} className="btn js-waves-off" data-action="toggle" data-class="nav-function-minify" title="Thu nhỏ">
              <i className="ni ni-minify-nav" />
            </a>
          </li>
          <li>
            <a href="/" onClick={(event)=>this.clickMenu(event)} className="btn js-waves-off" data-action="toggle" data-class="nav-function-fixed" title="Khóa">
              <i className="ni ni-lock-nav" />
            </a>
          </li>
        </ul>
      </div>
      {/* DOC: mobile button appears during mobile width */}
      <div className="hidden-lg-up">
        <a href="/" className="header-btn btn press-scale-down waves-effect waves-themed" data-action="toggle" data-class="mobile-nav-on">
          <i className="ni ni-menu" />
        </a>
      </div>
      <HeaderSearch/>
      <div className="ml-auto d-flex">
        {/* activate app search icon (mobile) */}
        <div className="hidden-sm-up">
          <a href="/" className="header-icon" data-action="toggle" data-class="mobile-search-on" data-focus="search-field" title="Search">
            <i className="fal fa-search" />
          </a>
        </div>
        {/* app settings */}
        <div className="hidden-md-down">
          <a href="/" className="header-icon" data-toggle="modal" data-target=".js-modal-settings">
            <i className="fal fa-cog" />
          </a>
        </div>
        {/* app shortcuts */}
        <HeaderMyApps/>
        {/* app message */}
        <a href="/" className="header-icon" data-toggle="modal" data-target=".js-modal-messenger">
          <i className="fal fa-globe" />
          <span className="badge badge-icon">!</span>
        </a>
        {/* app notification */}
        <HeaderNotifi/>
        {/* app user menu */}
        <HeaderAccount/>
      </div>
    </header>

        );
    }
}

export default Header;
