import React, { Component } from 'react';
import {connect} from 'react-redux';
import { Redirect,Link} from "react-router-dom";
import {tblUser} from '../Firebaseconnect';

class Login extends Component {
  constructor(props) {
    super(props);
    this.state ={
    }
  }
  isChange = (event) => {
    const ten = event.target.name;
    const giatri = event.target.value;
    //console.log(ten);
    //console.log(giatri);
    this.setState({
      [ten]:giatri
    });
          
  }
    _checkLogin(event){
      event.preventDefault();
      var status = false;
    //    console.log(this.props.isHome);
    // nhận thông tin user và pass kiểm tra trên csdl 
    // sau đó trả kết quả về hàm của store trả về
   //     this.props.isHomeFunc();
    //    console.log(this.props.isHome);
    console.log('Xữ lý login');
    console.log(this.state);
    tblUser.on('value',(dulieu) => {
      console.log('user:'+ JSON.stringify(dulieu.val()));
      for (const key in dulieu.val()) {
          // console.log(dulieu.val()[key].email);
          // console.log(this.state.username);
          // console.log(dulieu.val()[key].password);
          // console.log(this.state.password);
          let email = dulieu.val()[key].email;
          let password = dulieu.val()[key].password;
          if(email===this.state.username && password===this.state.password){
            console.log('Có TK');
            status =true;
            break;
            
          }
      }  
      //console.log("status "+ status);
      if(status === true) {
        this.props.isHomeFunc();  
      }
    });
  
    }
    render() {
        if(this.props.isHome===true){
            return <Redirect to="/Home" />;
        }
        return (
            // <div>
            //     <button type="button" className="btn btn-primary" onClick={()=>this._checkLogin()}>Login</button>
            //     <ul>
	          //     <li><Link to={"/register"}>Register</Link></li>  
            //     <li><Link to={"/forget"}>Forget</Link></li>
            //     <li><Link to={"/test"}>Test</Link></li>
            //     </ul>		
            // </div>
            <div>
  <div className="blankpage-form-field">
    <div className="page-logo m-0 w-100 align-items-center justify-content-center rounded border-bottom-left-radius-0 border-bottom-right-radius-0 px-4">
      <a href="/" className="page-logo-link press-scale-down d-flex align-items-center">
        <img src="img/logo.png" alt="SmartAdmin WebApp" aria-roledescription="logo" />
        <span className="page-logo-text mr-1">Đăng nhập</span>
        <i className="fal fa-angle-down d-inline-block ml-1 fs-lg color-primary-300" />
      </a>
    </div>
    <div className="card p-4 border-top-left-radius-0 border-top-right-radius-0">
      <form action="intel_introduction.html">
        <div className="form-group">
          <label className="form-label" htmlFor="username">Username</label>
          <input type="email" name="username" id="username" className="form-control" placeholder="your id or email"  onChange={(event)=>this.isChange(event)} />
          {/* <span className="help-block">
            Your unique username to app
          </span> */}
        </div>
        <div className="form-group">
          <label className="form-label" htmlFor="password">Password</label>
          <input type="password" name="password"  id="password" className="form-control" placeholder="password"  onChange={(event)=>this.isChange(event)} />
          {/* <span className="help-block">
            Your password
          </span> */}
        </div>
        <div className="form-group text-left">
          <div className="custom-control custom-checkbox">
            <input type="checkbox" className="custom-control-input" name="rememberme" id="rememberme" onChange={(event)=>this.isChange(event)} />
            <label className="custom-control-label" htmlFor="rememberme"> Ghi nhớ thông tin</label>
          </div>
        </div>
        <button type="submit" className="btn btn-default float-right" onClick={(event)=>this._checkLogin(event)}>Đăng nhập</button>
      </form>
    </div>
    <div className="blankpage-footer text-center">
      <Link to="/"><strong>Quên Password</strong></Link> | 
      <Link to="/register"><strong>Đăng ký tài khoản</strong></Link>
    </div>
  </div>
  <div className="login-footer p-2">
    <div className="row">
      <div className="col col-sm-12 text-center">
        {/* <i><strong>System Message:</strong> You were logged out from 198.164.246.1 on Saturday, March, 2017 at 10.56AM</i> */}
      </div>
    </div>
  </div>
  <video poster="img/backgrounds/clouds.png" id="bgvid" playsInline autoPlay muted loop>
    <source src="media/video/cc.webm" type="video/webm" />
    <source src="media/video/cc.mp4" type="video/mp4" />
  </video>
</div>
        );
    }
}
const mapStateToProps = (state, ownProps) => {
    return {
      isHome: state.isHome,
      
    }
  }
  // this.props.testStore
  const mapDispatchToProps = (dispatch, ownProps) => {
    return {
      isHomeFunc: () => {
        dispatch({type:"IS_HOME"})
      }
    }
  }
  // this.props.addData()
  
  export default connect(mapStateToProps, mapDispatchToProps)(Login);
