import React, { Component } from 'react';

export class DataPicker extends Component {
    render() {
        return (
            <main id="js-page-content" role="main" className="page-content">
  <ol className="breadcrumb page-breadcrumb">
    <li className="breadcrumb-item"><a href="/">SmartAdmin</a></li>
    <li className="breadcrumb-item">Form Plugins</li>
    <li className="breadcrumb-item active">Date Picker</li>
    <li className="position-absolute pos-top pos-right d-none d-sm-block"><span className="js-get-date" /></li>
  </ol>
  <div className="subheader">
    <h1 className="subheader-title">
      <i className="subheader-icon fal fa-credit-card-front" /> Date Picker<sup className="badge badge-primary fw-500">ADDON</sup>
      <small>
        Bootstrap-datepicker provides a flexible datepicker widget in the Bootstrap style.
      </small>
    </h1>
  </div>
  <div className="alert alert-primary">
    <div className="d-flex flex-start w-100">
      <div className="mr-2 hidden-md-down">
        <span className="icon-stack icon-stack-lg">
          <i className="base base-6 icon-stack-3x opacity-100 color-primary-500" />
          <i className="base base-10 icon-stack-2x opacity-100 color-primary-300 fa-flip-vertical" />
          <i className="ni ni-blog-read icon-stack-1x opacity-100 color-white" />
        </span>
      </div>
      <div className="d-flex flex-fill">
        <div className="flex-fill">
          <span className="h5">About</span>
          <p>Bootstrap datepicker allows the user to enter a date by merely clicking on a date in the pop-up calendar as opposed to having to take their hand off the mouse to type in a date. The UI makes easy to select the date and keep the proper formatting</p>
          <p className="m-0">
            Find in-depth, guidelines, tutorials and more on Bootstrap Datepicker's <a href="https://bootstrap-datepicker.readthedocs.io/en/stable/" >Official Documentation</a>
          </p>
        </div>
      </div>
    </div>
  </div>
  <div className="row">
    <div className="col-xl-12">
      <div id="panel-1" className="panel">
        <div className="panel-hdr">
          <h2>
            Datepicker <span className="fw-300"><i>Examples</i></span>
          </h2>
          <div className="panel-toolbar">
            <button className="btn btn-panel" data-action="panel-collapse" data-toggle="tooltip" data-offset="0,10" data-original-title="Collapse" />
            <button className="btn btn-panel" data-action="panel-fullscreen" data-toggle="tooltip" data-offset="0,10" data-original-title="Fullscreen" />
            <button className="btn btn-panel" data-action="panel-close" data-toggle="tooltip" data-offset="0,10" data-original-title="Close" />
          </div>
        </div>
        <div className="panel-container show">
          <div className="panel-content">
            <div className="panel-tag">
              Below you will find various date picker examples which you can use for your project
            </div>
            <div className="form-group row">
              <label className="col-form-label col-12 col-lg-3 form-label text-lg-right">Minimum Setup</label>
              <div className="col-12 col-lg-6 ">
                <input type="text" className="form-control" id="datepicker-1" readOnly placeholder="Select date" />
              </div>
            </div>
            <div className="form-group row">
              <label className="col-form-label col-12 col-lg-3 form-label text-lg-right">Input Group Setup</label>
              <div className="col-12 col-lg-6">
                <div className="input-group">
                  <input type="text" className="form-control " readOnly placeholder="Select date" id="datepicker-2" />
                  <div className="input-group-append">
                    <span className="input-group-text fs-xl">
                      <i className="fal fa-calendar" />
                    </span>
                  </div>
                </div>
              </div>
            </div>
            <div className="form-group row">
              <label className="col-form-label col-12 col-lg-3 form-label text-lg-right">Enable Helper Buttons</label>
              <div className="col-12 col-lg-6">
                <div className="input-group">
                  <input type="text" className="form-control " readOnly defaultValue="07/17/2020" id="datepicker-3" />
                  <div className="input-group-append">
                    <span className="input-group-text fs-xl">
                      <i className="fal fa-calendar-alt" />
                    </span>
                  </div>
                </div>
                <span className="help-block">Enable clear and today helper buttons</span>
              </div>
            </div>
            <div className="form-group row">
              <label className="col-form-label col-12 col-lg-3 form-label text-lg-right">Orientation</label>
              <div className="col-12 col-lg-6 demo-v-spacing-sm">
                <div className="input-group">
                  <input type="text" className="form-control " placeholder="Top left" id="datepicker-4-1" />
                  <div className="input-group-append">
                    <span className="input-group-text fs-xl">
                      <i className="fal fa-calendar-check" />
                    </span>
                  </div>
                </div>
                <div className="input-group">
                  <input type="text" className="form-control " placeholder="Top right" id="datepicker-4-2" />
                  <div className="input-group-append">
                    <span className="input-group-text fs-xl">
                      <i className="fal fa-calendar-times" />
                    </span>
                  </div>
                </div>
                <div className="input-group">
                  <input type="text" className="form-control " placeholder="Bottom left" id="datepicker-4-3" />
                  <div className="input-group-append">
                    <span className="input-group-text fs-xl">
                      <i className="fal fa-calendar-exclamation" />
                    </span>
                  </div>
                </div>
                <div className="input-group">
                  <input type="text" className="form-control " placeholder="Bottom right" id="datepicker-4-4" />
                  <div className="input-group-append">
                    <span className="input-group-text fs-xl">
                      <i className="fal fa-calendar-plus" />
                    </span>
                  </div>
                </div>
              </div>
            </div>
            <div className="form-group row">
              <label className="col-form-label col-12 col-lg-3 form-label text-lg-right">Range Picker</label>
              <div className="col-12 col-lg-6">
                <div className="input-daterange input-group" id="datepicker-5">
                  <input type="text" className="form-control" name="start" />
                  <div className="input-group-append input-group-prepend">
                    <span className="input-group-text fs-xl"><i className="fal fa-ellipsis-h" /></span>
                  </div>
                  <input type="text" className="form-control" name="end" />
                </div>
              </div>
            </div>
            <div className="form-group row">
              <label className="col-form-label col-12 col-lg-3 form-label text-lg-right">Inline Mode</label>
              <div className="col-12 col-lg-6">
                <div className id="datepicker-6"></div> 
              </div>
            </div>
            <div className="form-group row">
              <label className="col-form-label col-12 col-lg-3 form-label text-lg-right">Modal Demos</label>
              <div className="col-12 col-lg-6">
                <a href="/" className="btn btn-primary" data-toggle="modal" data-target="#default-example-modal">Show inside datepicker</a>
              </div>
            </div>
            {/* modal */}
            <div className="modal fade" id="default-example-modal" tabIndex={-1} role="dialog" aria-hidden="true">
              <div className="modal-dialog" role="document">
                <div className="modal-content">
                  <div className="modal-header">
                    <h4 className="modal-title">
                      Date picker inside modal
                    </h4>
                    <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                      <span aria-hidden="true"><i className="fal fa-times" /></span>
                    </button>
                  </div>
                  <div className="modal-body">
                    <div className="form-group">
                      <label className="form-label" htmlFor="datepicker-modal-2">Select a date</label>
                      <div className="input-group">
                        <div className="input-group-prepend">
                          <span className="input-group-text fs-xl"><i className="fal fa-calendar" /></span>
                        </div>
                        <input type="text" id="datepicker-modal-2" className="form-control" placeholder="Select a date" aria-label="date" aria-describedby="datepicker-modal-2" />
                      </div>
                      <span className="help-block">Some help content goes here</span>
                    </div>
                    <div className="form-group">
                      <label className="form-label" htmlFor="datepicker-modal-3">Datepicker with button</label>
                      <div className="input-group">
                        <input type="text" id="datepicker-modal-3" className="form-control" placeholder="End date" aria-label="date" aria-describedby="datepicker-modal-3" />
                        <div className="input-group-append">
                          <span className="input-group-text fs-xl"><i className="fal fa-calendar-alt" /></span>
                        </div>
                      </div>
                      <span className="help-block">Some help content goes here</span>
                    </div>
                  </div>
                  <div className="modal-footer">
                    <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                    <button type="button" className="btn btn-primary">Save changes</button>
                  </div>
                </div>
              </div>
            </div>
            {/* modal end */}
          </div>
        </div>
      </div>
    </div>
  </div>
</main>

        );
    }
}

export default DataPicker;
