import React, { Component } from 'react';

class Footer extends Component {
  clickMenu(event){
    event.preventDefault();
  }
    render() {
        return (
          
      <footer className="page-footer" role="contentinfo">
      <div className="d-flex align-items-center flex-1 text-muted">
        <span className="hidden-md-down fw-700">2019 © Từ Ngọc Vân - <a href="/" onClick={(event)=>this.clickMenu(event)} className="text-primary fw-500" title="gotbootstrap.com" >tungocvan.site</a></span>
      </div>
      <div>
        <ul className="list-table m-0">
          <li><a href="/" onClick={(event)=>this.clickMenu(event)} className="text-secondary fw-700">Về chúng tôi</a></li>
          <li className="pl-3"><a href="/" onClick={(event)=>this.clickMenu(event)} className="text-secondary fw-700">Bản quyền</a></li>
          <li className="pl-3"><a href="/" onClick={(event)=>this.clickMenu(event)} className="text-secondary fw-700">Hướng dẫn sử dụng</a></li>
          <li className="pl-3 fs-xl"><a href="/" onClick={(event)=>this.clickMenu(event)} className="text-secondary"><i className="fal fa-question-circle" aria-hidden="true" /></a></li>
        </ul>
      </div>
    </footer>

        );
    }
}

export default Footer;