import React, { Component } from 'react';

export class AsideInfo extends Component {
  clickMenu(event){
    event.preventDefault();
  }
    render() {
        return (
            
          <div className="info-card">
          <img src="img/demo/avatars/avatar-admin.png" className="profile-image rounded-circle" alt="Tu Ngoc Van" />
          <div className="info-card-text">
            <a href="/" onClick={(event)=>this.clickMenu(event)} className="d-flex align-items-center text-white">
              <span className="text-truncate text-truncate-sm d-inline-block">
                Từ Ngọc Vân
              </span>
            </a>
            <span className="d-inline-block text-truncate text-truncate-sm">Administrator</span>
          </div>
          <img src="img/card-backgrounds/cover-2-lg.png" className="cover" alt="cover" />
          {/* <a href="/" onClick={(event)=>this.clickMenu(event)} className="pull-trigger-btn" data-action="toggle" data-class="list-filter-active" data-target=".page-sidebar" data-focus="nav_filter_input">
            <i className="fal fa-angle-down" />
          </a> */}
        </div>
        
        );
    }
}

export default AsideInfo;
