import React, { Component } from 'react';

class Shortmenu extends Component {
  clickMenu(event){
    event.preventDefault();
  }
    render() {
        return (
            <nav className="shortcut-menu d-none d-sm-block">
            <input type="checkbox" className="menu-open" name="menu-open" id="menu_open" />
            <label htmlFor="menu_open" className="menu-open-button ">
              <span className="app-shortcut-icon d-block" />
            </label>
            <a href="/" onClick={(event)=>this.clickMenu(event)}  className="menu-item btn waves-effect waves-themed" data-toggle="tooltip" data-placement="left" data-original-title="Scroll Top">
              <i className="fal fa-arrow-up" />
            </a>
            <a href="page_login-alt.html" className="menu-item btn waves-effect waves-themed" data-toggle="tooltip" data-placement="left" data-original-title="Logout">
              <i className="fal fa-sign-out" />
            </a>
            <a href="/" onClick={(event)=>this.clickMenu(event)}  className="menu-item btn waves-effect waves-themed" data-action="app-fullscreen" data-toggle="tooltip" data-placement="left" data-original-title="Full Screen">
              <i className="fal fa-expand" />
            </a>
            <a href="/" onClick={(event)=>this.clickMenu(event)}  className="menu-item btn waves-effect waves-themed" data-action="app-print" data-toggle="tooltip" data-placement="left" data-original-title="Print page">
              <i className="fal fa-print" />
            </a>
            <a href="/" onClick={(event)=>this.clickMenu(event)}  className="menu-item btn waves-effect waves-themed" data-action="app-voice" data-toggle="tooltip" data-placement="left" data-original-title="Voice command">
              <i className="fal fa-microphone" />
            </a>
          </nav>
          
        );
    }
}

export default Shortmenu;