import React, { Component } from 'react';

class HeaderSearch extends Component {
    render() {
        return (
            <div className="search">
        <form className="app-forms hidden-xs-down" role="search" action="/" autoComplete="off">
          <input type="text" id="search-field" placeholder="Tìm kiếm" className="form-control" tabIndex={1} />
          <a href="/" onClick={(event)=>this.clickMenu(event)}  className="btn-danger btn-search-close js-waves-off d-none" data-action="toggle" data-class="mobile-search-on">
            <i className="fal fa-times" />
          </a>
        </form>
      </div>
        );
    }
}

export default HeaderSearch;