import React, { Component } from 'react';
import {connect} from 'react-redux';
class AsideMenu extends Component {
    constructor(props) {
        super(props);
        this.state={
            menu:[],
        }
    }
    
    componentWillMount() {
        var Menu = [
            {"menu_name":"Bán hàng","menu_icon":"fal fa-paper-plane","menu_link":"/","strComponent":"Bh","menu_sub":""},
            {"menu_name":"Tổng quan","menu_icon":"fal fa-home","menu_link":"/","strComponent":"Tq","menu_sub":""},
            {"menu_name":"Hàng hóa","menu_icon":"fal fa-shopping-basket","menu_link":"/","strComponent":"Hh",
            "menu_sub":[{"menu_name":"Thiết lập giá","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item2"}]},
            {"menu_name":"Danh mục","menu_icon":"fal fa-rocket","menu_link":"/","strComponent":"Dm",
            "menu_sub":[{"menu_name":"Đồ uống - Món ăn","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item3"},{"menu_name":"Nhóm thực đơn","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"},{"menu_name":"Topping, ghi chú món","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"},{"menu_name":"Khu vực","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"},{"menu_name":"Phòng bàn","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"},{"menu_name":"Nhân viên","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"},{"menu_name":"Khách hàng","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"},{"menu_name":"Nhà cung cấp","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"}]},
            {"menu_name":"Kho","menu_icon":"fal fa-map-marker","menu_link":"/","strComponent":"Kh",
            "menu_sub":[{"menu_name":"Báo cáo kho","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item3"},{"menu_name":"Nhập xuất kho","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"},{"menu_name":"Danh mục kho","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"}]},
            {"menu_name":"Khuyến mại","menu_icon":"fal fa-gift","menu_link":"/","strComponent":"Km","menu_sub":""},
            {"menu_name":"Báo cáo","menu_icon":"fal fa-newspaper","menu_link":"/","strComponent":"Bc",
            "menu_sub":[{"menu_name":"Theo đơn hàng","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item3"},{"menu_name":"Theo bàn","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"},{"menu_name":"Theo khách hàng","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"},{"menu_name":"Doanh thu theo nhóm","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"},{"menu_name":"Mặt hàng bán chạy","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"},{"menu_name":"Lợi nhuận","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"},{"menu_name":"Khách hàng trả trước","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"},{"menu_name":"Quỹ tiền","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"},{"menu_name":"Công nợ khách hàng","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"}]},
            {"menu_name":"Thiết lập","menu_icon":"fal fa-cogs","menu_link":"/","strComponent":"Tl",
            "menu_sub":[{"menu_name":"Thiết lập của hàng","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item3"},{"menu_name":"Thiết lập in","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"},{"menu_name":"Thuế và phí vận Chuyển","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"},{"menu_name":"Thiết lập vai trò","menu_icon":"fal fa-info-circle","menu_link":"/","strComponent":"Item"}]},
            ];
        this.setState({
            menu:Menu,
            keyMenu:null
        })
        //console.log('location.href: '+ window.location.href); 
    }
    _getItem(event,key,name){
      event.preventDefault();
    //  console.log(key+" - "+name);
      this.props.getItem(name);
    }
    Hienmenu(event,key,name){
        event.preventDefault();
       // console.log(key +"hienmenu:"+name);
   
        this.props.getItem(name);
        var dropdown = document.getElementsByClassName("dropdown-btn");
        let item = dropdown[0].nextElementSibling.className ;
        var items = document.getElementsByClassName(item);
        if(this.state.keyMenu!==null && this.state.keyMenu!==key) items[this.state.keyMenu].style.display="" ;
            if(items[key].style.display==="block"){
              items[key].style.display="" ;
            } else {
              items[key].style.display="block" ;
            }
            this.setState({keyMenu:key})
      }
    
      ShowMenu(){
       //console.log(this.state.menu);
 
        return this.state.menu.map((value,key) => {
          if(value.menu_sub) {
            var muiTenSub = 'fal fa-angle-down';
          }
            return(
                <li key={key}>
                  <a href="/" onClick={(event)=>this.Hienmenu(event,key,value.strComponent)}  title="Application Intel" data-filter-tags="application intel" className="dropdown-btn waves-effect waves-themed" aria-expanded="false">
                    <i style={{color:'white'}} className={value.menu_icon} />
                    <span className="nav-link-text" data-i18n="nav.application_intel">{value.menu_name}</span>
                    <b className="collapse-sign"><em style={{color:'white'}} className={muiTenSub} /></b></a>
                    <ul className="dropdown-container">
                      {
                          this.ShowSubMenu(value.menu_sub)
                      }
                    </ul>
                    
                </li>
            )
            
        });
      }
      ShowSubMenu(subMenu){
       // console.log(subMenu);
       if(subMenu){
       return subMenu.map((value,key) => {
        //    console.log(element.menu_name);
            return(
                    <li key={key} style={{paddingLeft:10,color:'white'}}>
                      <a  href={value.menu_link} onClick={(event)=>this._getItem(event,key,value.strComponent)} title="Analytics Dashboard" data-filter-tags="application intel analytics dashboard" className=" waves-effect waves-themed">
                        <i className={value.menu_icon} />  
                        <span  style={{color:'white'}} className="nav-link-text" data-i18n="nav.application_intel_analytics_dashboard">{value.menu_name}</span>
                      </a>
                    </li>
            
            )
        });
      }
        // return subMenu.map((value,key) => {
        //     return(
        //           <ul key={key} className="dropdown-container">
        //             <li>
        //               <a href={value.menu_link} title="Analytics Dashboard" data-filter-tags="application intel analytics dashboard" className=" waves-effect waves-themed">
        //                 <i className={value.menu_icon} />  
        //                 <span className="nav-link-text" data-i18n="nav.application_intel_analytics_dashboard">{value.menu_name}</span>
        //               </a>
        //             </li>
        //           </ul>
               
        //     )
        // });
      }
    render() {
        
        return (
            <ul id="js-nav-menu" className="nav-menu">{this.ShowMenu()}</ul>  
    
        );
    }
}
const mapStateToProps = (state, ownProps) => {
  return {
    isHome: state.isHome,
    
  }
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    getItem: (item) => {
      dispatch({type:"GET_ITEM",item})
    }
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(AsideMenu);
