import React, { Component } from 'react';

class HeaderAccount extends Component {
  clickMenu(event){
    event.preventDefault();
  }
    render() {
        return (
            <div>
            <a href="/" onClick={(event)=>this.clickMenu(event)}  data-toggle="dropdown" title="Cài đặt" className="header-icon d-flex align-items-center justify-content-center ml-2">
              <img src="img/demo/avatars/avatar-admin.png" className="profile-image rounded-circle" alt="Dr. Codex Lantern" />
              {/* you can also add username next to the avatar with the codes below:
                                    <span class="ml-1 mr-1 text-truncate text-truncate-header hidden-xs-down">Me</span>
                                    <i class="ni ni-chevron-down hidden-xs-down"></i> */}
            </a>
            <div className="dropdown-menu dropdown-menu-animated dropdown-lg">
              <div className="dropdown-header bg-trans-gradient d-flex flex-row py-4 rounded-top">
                <div className="d-flex flex-row align-items-center mt-1 mb-1 color-white">
                  <span className="mr-2">
                    <img src="img/demo/avatars/avatar-admin.png" className="rounded-circle profile-image" alt="Dr. Codex Lantern" />
                  </span>
                  <div className="info-card-text">
                    <div className="fs-lg text-truncate text-truncate-lg">Từ Ngọc Vân</div>
                    <span className="text-truncate text-truncate-md opacity-80">tungocvan.site</span>
                  </div>
                </div>
              </div>
              <div className="dropdown-divider m-0" />
              <a href="/" onClick={(event)=>this.clickMenu(event)}  className="dropdown-item" data-action="app-reset">
                <span data-i18n="drpdwn.reset_layout">Reset Layout</span>
              </a>
              <a href="/" onClick={(event)=>this.clickMenu(event)}  className="dropdown-item" data-toggle="modal" data-target=".js-modal-settings">
                <span data-i18n="drpdwn.settings">Settings</span>
              </a>
              <div className="dropdown-divider m-0" />
              <a href="/" onClick={(event)=>this.clickMenu(event)}  className="dropdown-item" data-action="app-fullscreen">
                <span data-i18n="drpdwn.fullscreen">Fullscreen</span>
                <i className="float-right text-muted fw-n">F11</i>
              </a>
              <a href="/" onClick={(event)=>this.clickMenu(event)}  className="dropdown-item" data-action="app-print">
                <span data-i18n="drpdwn.print">Print</span>
                <i className="float-right text-muted fw-n">Ctrl + P</i>
              </a>
              <div className="dropdown-multilevel dropdown-multilevel-left">
                <div className="dropdown-item">
                  Language
                </div>
                <div className="dropdown-menu">
                  <a href="/?lang=fr" onClick={(event)=>this.clickMenu(event)}  className="dropdown-item" data-action="lang" data-lang="fr">Français</a>
                  <a href="/?lang=en" onClick={(event)=>this.clickMenu(event)}  className="dropdown-item active" data-action="lang" data-lang="en">English (US)</a>
                  <a href="/?lang=es" onClick={(event)=>this.clickMenu(event)}  className="dropdown-item" data-action="lang" data-lang="es">Español</a>
                  <a href="/?lang=ru" onClick={(event)=>this.clickMenu(event)}  className="dropdown-item" data-action="lang" data-lang="ru">Русский язык</a>
                  <a href="/?lang=jp" onClick={(event)=>this.clickMenu(event)}  className="dropdown-item" data-action="lang" data-lang="jp">日本語</a>
                  <a href="/?lang=ch" onClick={(event)=>this.clickMenu(event)}  className="dropdown-item" data-action="lang" data-lang="ch">中文</a>
                </div>
              </div>
              <div className="dropdown-divider m-0" />
              <a className="dropdown-item fw-500 pt-3 pb-3" href="/" onClick={(event)=>this.clickMenu(event)} >
                <span data-i18n="/">Logout</span>
                <span className="float-right fw-n">@tungocvan</span>
              </a>
            </div>
          </div>
        );
    }
}

export default HeaderAccount;