import React, { Component } from 'react';
import { Redirect } from "react-router-dom";
import {connect} from 'react-redux';
import Shorticon from './Main/Shorticon';
import Shortmenu from './Main/Shortmenu';
import Libs from './Main/Libs';
import Aside from './Main/Aside';
import Header from './Main/Header';
import Footer from './Main/Footer';
import Main from './Main/Main';
import DataPicker from './Main/DataPicker';
import Warnning from './Main/Warnning';
export class Home extends Component {
    componentWillMount(){
        console.log(this.props.isHome);
        if(this.props.isHome){
            localStorage.setItem('isHome',this.props.isHome);
        }
        
    }
    constructor(props) {
        super(props);
        this.state = {
            idSub:-1,
            idItem:"",
            isHome:false
        }
    }
    _getComponent(strComp){
      console.log('strComp: '+strComp);
      
       if(strComp==="Bh") {
        return <Main /> ;
       } else {
        return <Warnning/>
       } 
      
    }
    render() {
        if(localStorage.getItem('isHome')===null) return false;
        return (
          <div>
          <div className="pace  pace-inactive"><div className="pace-progress" data-progress-text="100%" data-progress={99} style={{transform: 'translate3d(100%, 0px, 0px)'}}>
            <div className="pace-progress-inner" />
          </div>
          <div className="pace-activity" /></div>
          <div className="page-wrapper">
          <div className="page-inner">
          <Aside/>
          <div className="page-content-wrapper">
        <Header/>
          {this._getComponent(this.props.getItem)}
     {/*     <MainTableBasic/>
          <DataPicker/>*/}
          <Footer/> 
          </div>
          </div>  
          </div> 
          <Shorticon/>
          <Shortmenu/>
          <Libs/>
        </div>

        );
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
      isHome: state.isHome,
      getItem: state.getItem,
      
    }
  }
  // this.props.testStore
  
  const mapDispatchToProps = (dispatch, ownProps) => {
    return {
      isHomeFunc: () => {
        dispatch({type:"IS_HOME"})
      }
    }
  }
  // this.props.addData()
  
  export default connect(mapStateToProps, mapDispatchToProps)(Home);
