import React, { Component } from 'react';

class AsideNaviSearch extends Component {
    clickMenu(event){
        event.preventDefault();
    }
    render() {
        return (
            
            <div className="nav-filter">
            <div className="position-relative">
            <input type="text" id="nav_filter_input" placeholder="Filter menu" className="form-control" tabIndex={0} />
            <a href="/" onClick={(event)=>this.clickMenu(event)} className="btn-primary btn-search-close js-waves-off" data-action="toggle" data-class="list-filter-active" data-target=".page-sidebar">
                <i className="fal fa-chevron-up" />
            </a>
            </div>
        </div>
  
        );
    }
}

export default AsideNaviSearch;